  async downloadCommentFile(url) {
    if (localStorage.modeType == 'Offline' && this.network.type == 'none') {
      return;
    }
    var mimeType = '';
    if (url.split('/').pop().split('.')[1] == 'pdf') {
      mimeType = 'application/pdf';
    } else {
      mimeType = 'image/' + url.split('/').pop().split('.')[1];
    }
    var name = url.substring(url.lastIndexOf('/') + 1);
    console.log(name);

    const loader = await this.loadingCtrl.create({
      //spinner: 'hide',
      message: "Downloading ..."
    });
    loader.present();

    const fileTransfer: FileTransferObject = this.transfer.create();
    fileTransfer.onProgress((progressEvent) => {
      var percent = progressEvent.loaded / progressEvent.total * 100;
      this.zone.run(() => {
        this.percentage = Math.round(percent);
        //  loader.set("Downloading " + this.percentage + "%");
      });
      console.log(this.percentage);
    });

    if (this.platform.is('ios')) {

      fileTransfer.download(this.urlPrefix + url, this.file.documentsDirectory + name).then(async (entry) => {
        console.log('download complete: ' + entry.toURL());
        const alert = await this.alertController.create({
          header: '',
          message: 'File Downloaded successfully',
          cssClass: 'secondary',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });

        await alert.present();
        loader.dismiss();
        loader.dismiss();
        this.fileOpener.open(entry.toURL(), 'application/pdf')
          .then(() => console.log('File is opened'))
          .catch(e => console.log('Error opening file', e));
      }, (error) => {
        // handle error
        console.log(error);
        loader.dismiss();
      });
    } else {
      fileTransfer.download(this.urlPrefix + url, this.file.externalDataDirectory + name).then(async (entry) => {
        console.log('download complete: ' + entry.toURL());
        const alert = await this.alertController.create({
          header: '',
          message: 'File Downloaded successfully',
          cssClass: 'secondary',
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: (blah) => {
                console.log('Confirm Cancel: blah');
              }
            }
          ]
        });

        await alert.present();
        loader.dismiss();
        this.fileOpener.open(entry.toURL(), mimeType)
          .then(() => console.log('File is opened'))
          .catch(e => console.log('Error opening file', e));
      }, (error) => {
        // handle error
        loader.dismiss();
      });
      // });
    }
  }



